/*-------------------------------------------------------------------*/
/* === Components === */
/* Accordions */

.accordion {
  border-radius: 8px;

  > .card {
    margin-bottom: .75rem;
    box-shadow: 0px 1px 15px 1px rgba(230, 234, 236, 0.35);
    border-radius: .25rem;
    border-radius: 8px;

    &:not(:last-of-type) {
      border-bottom: 0;
      border-bottom-right-radius: 8;
      border-bottom-left-radius: 8;
      border-radius: 8px;
    }

    &:not(:first-of-type) {
      border-top-left-radius: 8;
      border-top-right-radius: 8;
      border-radius: 8px;
    }

    .card-header {
      background-color: transparent;
      border: none;
      padding: 2rem;
      border-radius: 8px;

      * {
        font-weight: 400;
        font-size: 1rem;
      }

      a {
        display: block;
        color: inherit;
        text-decoration: none;
        font-size: inherit;
        position: relative;
        -webkit-transition: color 0.5s ease;
        -moz-transition: color 0.5s ease;
        -ms-transition: color 0.5s ease;
        -o-transition: color 0.5s ease;
        transition: color 0.5s ease;
        padding-right: 1.5rem;

        &:before {
          font-family: "Font Awesome 5 Pro";
          position: absolute;
          right: 7px;
          top: 0;
          font-size: 14px;
          display: block;
        }

        &[aria-expanded="true"] {
          color: #4B49AC;

          &:before {
            content: "\e622";
          }
        }

        &[aria-expanded="false"]:before {
          content: "\e61a";
        }
      }
    }

    .card-body {
      font-size: 14px;
      padding: 0 2rem 2rem 2rem;
      font-weight: 400;
      line-height: 1.5;

      i {
        font-size: 1.25rem;
      }
    }
  }

  &.accordion-bordered {
    background: #ffffff;
    box-shadow: 0px 1px 15px 1px rgba(230, 234, 236, 0.35);

    .card {
      margin: 0 2rem;
      border-top: 1px solid #CED4DA;
      box-shadow: none;
      border-radius: 0;
      border-left: 0;
      border-right: 0;

      .card-header, .card-body {
        padding-left: 0;
        padding-right: 0;
      }

      .card-header a {
        &:before {
          color: #FF4747;
        }

        &[aria-expanded="true"] {
          color: inherit;

          &:before {
            content: "\e65c";
          }
        }

        &[aria-expanded="false"]:before {
          content: "\e65f";
        }
      }

      &:first-child {
        border-top: 0;
      }

      &:last-child {
        border-bottom: 0;
      }
    }
  }

  &.accordion-filled .card {
    padding: 0;

    .card-header {
      padding: 0;

      a {
        padding: 2rem 4.5rem 2rem 2rem;
        -webkit-transition: all 0.2s linear;
        -moz-transition: all 0.2s linear;
        -ms-transition: all 0.2s linear;
        -o-transition: all 0.2s linear;
        transition: all 0.2s linear;

        &:before {
          top: 40%;
          right: 40px;
        }

        &[aria-expanded="true"] {
          background: #248AFD;
          color: #ffffff;

          &:before {
            content: "\e648";
            color: #ffffff;
          }
        }

        &[aria-expanded="false"]:before {
          content: "\f078";
        }
      }
    }

    .card-body {
      padding: 0 2rem 2rem 2rem;
      background: #248AFD;
      color: #ffffff;
    }
  }

  &.accordion-solid-header .card {
    padding: 0;

    .card-header {
      padding: 0;

      a {
        padding: 2rem 4.5rem 2rem 2rem;
        -webkit-transition: all 0.2s linear;
        -moz-transition: all 0.2s linear;
        -ms-transition: all 0.2s linear;
        -o-transition: all 0.2s linear;
        transition: all 0.2s linear;

        &:before {
          top: 40%;
          right: 40px;
        }

        &[aria-expanded="true"] {
          background: #4B49AC;
          color: #ffffff;
          border-radius: 8px;

          &:before {
            content: "\e648";
            color: #ffffff;
          }
        }

        &[aria-expanded="false"]:before {
          content: "\f078";
        }
      }
    }

    .card-body {
      padding: 2rem;
    }
  }

  &.accordion-solid-content .card {
    padding: 0;

    .card-header {
      padding: 0;

      a {
        padding: 2rem 4.5rem 2rem 2rem;
        -webkit-transition: all 0.2s linear;
        -moz-transition: all 0.2s linear;
        -ms-transition: all 0.2s linear;
        -o-transition: all 0.2s linear;
        transition: all 0.2s linear;

        &:before {
          top: 40%;
          right: 40px;
        }

        &[aria-expanded="true"] {
          color: inherit;

          &:before {
            content: "\e648";
          }
        }

        &[aria-expanded="false"]:before {
          content: "\f078";
        }
      }
    }

    .card-body {
      padding: 2rem;
      background: #57B657;
      color: #ffffff;
      border-radius: 8px;
    }
  }

  &.accordion-multi-colored .card {
    .card-header, .card-body {
      background: transparent;
      color: #ffffff;
    }

    &:nth-child(1) {
      background: #4B49AC;
    }

    &:nth-child(2) {
      background: #57B657;
    }

    &:nth-child(3) {
      background: #FF4747;
    }

    &:nth-child(4) {
      background: #FFC100;
    }

    &:nth-child(5) {
      background: #248AFD;
    }

    .card-header a {
      &[aria-expanded="true"] {
        color: inherit;

        &:before {
          content: "\e648";
        }
      }

      &[aria-expanded="false"]:before {
        content: "\f078";
      }
    }
  }
}

/* Badges */

.badge {
  border-radius: 20px;
  font-size: 12px;
  line-height: 1;
  padding: .375rem .5625rem;
  font-weight: normal;

  &.badge-pill {
    border-radius: 10rem;
  }
}

/*Badge outlined variations*/

.badge-outline-primary {
  color: #4B49AC;
  border: 1px solid #4B49AC;
}

.badge-outline-secondary {
