/* Breadcrumbs */

.breadcrumb {
  border: 1px solid #CED4DA;

  .breadcrumb-item {
    font-size: 0.875rem;
  }

  &.breadcrumb-custom {
    padding: 0 0;
    border-color: #dbe3e6;

    .breadcrumb-item {
      font-size: 0.875rem;
      background: #dbe3e6;
      padding: 0.56rem 12px;
      color: #000;
      display: inline-block;
      vertical-align: top;

      &:last-child {
        background: transparent;
      }

      &:before {
        content: "";
        padding-right: 0;
      }

      a {
        position: relative;
        color: inherit;
        border: 1px solid #dbe3e6;
        display: inline-block;
        vertical-align: top;

        &:before, &:after {
          position: absolute;
          top: -9px;
          width: 0;
          height: 0;
          content: "";
          border-top: 21px solid transparent;
          border-bottom: 21px solid transparent;
        }

        &:before {
          right: -22px;
          z-index: 3;
          border-left-color: #dbe3e6;
          border-left-style: solid;
          border-left-width: 12px;
        }

        &:after {
          border-top: 21px solid transparent;
          border-bottom: 22px solid transparent;
          border-left: 12px solid #ffffff;
          top: -9px;
          right: -23px;
        }
      }

      span {
        display: inline-block;
        vertical-align: top;
      }

      > .breadcrumb-item {
        padding-right: 0;
      }
    }
  }

  &.bg-success {
    border: none;
  }
}

