/* Wizard */

.wizard {
  display: block;
  width: 100%;
  overflow: hidden;

  a {
    outline: 0;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    > li {
      display: block;
      padding: 0;
    }
  }

  > {
    .steps {
      position: relative;
      display: block;
      width: 100%;

      .current-info {
        position: absolute;
        left: -999em;
      }

      .number {
        font-size: 0.875rem;
      }

      > ul > li {
        width: 25%;
        float: left;
        text-align: center;
      }

      a {
        display: block;
        width: auto;
        margin: 0 0.5em 0.5em;
        padding: 1em 1em;
        text-decoration: none;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
        font-size: 0.875rem;
        font-weight: 600;

        &:hover, &:active {
          display: block;
          width: auto;
          margin: 0 0.5em 0.5em;
          padding: 1em;
          text-decoration: none;
          -webkit-border-radius: 5px;
          -moz-border-radius: 5px;
          border-radius: 5px;
        }
      }

      .disabled a {
        background: #e6e9ed;
        color: #434a54;
        cursor: default;

        &:hover {
          background: #e6e9ed;
          color: #434a54;
          cursor: default;
        }

        &:active {
          background: #e6e9ed e;
          color: #434a54;
          cursor: default;
        }
      }

      .current a {
        background: #4B49AC;
        color: #ffffff;
        cursor: default;

        &:hover, &:active {
          background: #4B49AC;
          color: #ffffff;
          cursor: default;
        }
      }

      .done a {
        background: #8e8dce;
        color: #ffffff;

        &:hover, &:active {
          background: #8e8dce;
          color: #ffffff;
        }
      }

      .error a {
        background: #FF4747;
        color: #fff;

        &:hover, &:active {
          background: #FF4747;
          color: #fff;
        }
      }
    }

    .content {
      background: #ffffff;
      display: block;
      margin: 0.5em;
      min-height: 24em;
      overflow: auto;
      position: relative;
      width: auto;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      border-radius: 5px;
      border: 1px solid #CED4DA;

      > {
        .title {
          position: absolute;
          left: -999em;
        }

        .body {
          float: left;
          position: absolute;
          width: 95%;
          height: 95%;
          padding: 2.5%;

          ul {
            list-style: disc;

            > li {
              display: list-item;
            }
          }

          > iframe {
            border: 0 none;
            width: 100%;
            height: 100%;
          }

          input {
            display: block;
            border: 1px solid #CED4DA;

            &[type="checkbox"] {
              display: inline-block;
            }

            &.error {
              background: #fbe3e4;
              border: 1px solid #ffadad;
              color: #FF4747;
            }
          }

          label {
            display: inline-block;
            margin-bottom: 0.5em;

            &.error {
              color: #FF4747;
              display: inline-block;
              margin-left: 1.5em;
            }
          }
        }
      }
    }

    .actions {
      padding-top: 20px;
      position: relative;
      display: block;
      text-align: right;
      width: 100%;

      > ul {
        display: inline-block;
        text-align: right;

        > li {
          float: left;
          margin: 0 0.5em;
        }
      }
    }
  }

  &.vertical > {
    .steps {
      display: inline;
      float: left;
      width: 30%;

      > ul > li {
        float: none;
        width: 100%;
        text-align: left;
      }
    }

    .content {
      display: inline;
      float: left;
      margin: 0 2.5% 0.5em 2.5%;
      width: 65%;
    }

    .actions {
      display: inline;
      float: right;
      margin: 0 2.5%;
      width: 95%;

      > ul > li {
        margin: 0 0 0 1em;
      }
    }
  }
}

@media (max-width: 767px) {
  .wizard > .steps > ul > li {
    width: 50%;
  }
}

@media (max-width: 767px) {
  .wizard.vertical > .steps {
    width: 42%;
  }
}

@media (max-width: 767px) {
  .wizard.vertical > .content {
    width: 52%;
  }
}

@media (max-width: 767px) {
  .wizard.vertical > .actions {
    margin: 0;
    width: 100%;
  }
}

